.Nav {
    background-color: black;
    max-height: 100vh;
    overflow: hidden;
    flex-direction: row;
    font-size: calc(2px + 2vmin);
    color: black;
    margin: 20px;
    border: 1px solid white;

    position: fixed; /* Set the navbar to fixed position */
    width: 95%;
    z-index: 100;
}

.Nav a {
    float: left;
    background-color: black;
    color: white;
    text-align: center;
    padding: 20px 30px;
    text-decoration: none;
    font-family: 'roboto';
    margin: 0;
}

.Nav a:hover {
    background-color: #ddd;
    color: black;
}

.Nav-right {
    float: right;
}
